import gql from 'graphql-tag';
import GRAPHQL from './index'

export default class {
  
  
  static getSellerProducts(data, callback) {
    const query = {
      query: gql`
        {
          saleGetSellerProducts(page: ${data.page}, pageSize:${data.pageSize}) {
            status
            message
            success
            data
          }
       }`
    }

    GRAPHQL.doQuery(query)
      .then(response => {
        
        try {
          return callback({
            status: 1,
            data: JSON.parse(response.data.saleGetSellerProducts.data)
          })
        } catch (e) {
          return callback({ status: 0, message: "HTTPERROR" })
        }
      })
      .catch(error => {
        return callback({ status: 0, message: error })
      })
  }

  static getProduct(id,callback) {
    const query = {
      query: gql`
        {
          saleGetSellerProduct(seller_product_id: "${id}") {
            status
            message
            success
            data
          }
       }`
    }

    GRAPHQL.doQuery(query)
      .then(response => {
        
        try {
          return callback({
            status: 1,
            data: JSON.parse(response.data.saleGetSellerProduct.data)
          })
        } catch (e) {
          return callback({ status: 0, message: "HTTPERROR" })
        }
      })
      .catch(error => {
        return callback({ status: 0, message: error })
      })
  }

  static postProduct(data,callback) {
    console.log("seller product object----->", data)
    const query = {
      mutation: gql`
        mutation {
          salePostSellerProduct(
            seller_product_id:"${data._id?data._id:''}",
            seller_user_id:"${data.seller_user_id}",
            product_id: "${data.product_id?data.product_id:''}", 
            seller_product_image: "${data.seller_product_image}",
            seller_product_identifier: "${data.seller_product_identifier}",
            seller_product_description: "${data.seller_product_description}",
            seller_product_in_stock_count:${data.seller_product_in_stock_count},
            seller_product_option_value_id:"${data.seller_product_option_value_id}",
            price:${data.price},
            lang_data:${data.lang_data},
          ) {
            status
            message
            success
            data
          }
        }    
      `
    }
    GRAPHQL.doMutate(query)
      .then(response => {
        try {
          return callback({
            status: 1,
            data: JSON.parse(response.data.salePostSellerProduct.data)
          })
        } catch (e) {
          return callback({ status: 0, message: e.Message })
        }
      })
      .catch(error => {
        return callback({ status: 0, message: error })
      })
  }


  static deleteProduct(product_id,callback) {
    const query = {
      mutation: gql`
          mutation{
            saleDeleteSellerProduct(
              seller_product_id:"${product_id}"
            ) {
              status
              message
              success
              data
            }
          }
        `
    }

    GRAPHQL.doMutate(query)
      .then(response => {
        try {
          return callback({
            status: 1,
            data: JSON.parse(response.data.saleDeleteSellerProduct.data)
          })
        } catch (e) {
          return callback({ status: 0, message: e.Message })
        }
      })
      .catch(error => {
        return callback({ status: 0, message: error })
      })
  }

 
}
