import gql from 'graphql-tag';
import GRAPHQL from './index'

export default class {  
  static getProducts(data, callback) {
    const query = {
      query: gql`
        {
          saleGetProducts(page: ${data.page}, pageSize:${data.pageSize}, lang_code: "en") {
            status
            message
            success
            data
          }
       }`
    }

    GRAPHQL.doQuery(query)
      .then(response => {
        try {
          return callback({
            status: 1,
            data: JSON.parse(response.data.saleGetProducts.data)
          })
        } catch (e) {
          return callback({ status: 0, message: "HTTPERROR" })
        }
      })
      .catch(error => {
        return callback({ status: 0, message: error })
      })
  }

  static getProduct(id,callback) {
    const query = {
      query: gql`
        {
          saleGetProduct(product_id: "${id}") {
            status
            message
            success
            data
          }
       }`
    }

    GRAPHQL.doQuery(query)
      .then(response => {
        try {
          return callback({
            status: 1,
            data: JSON.parse(response.data.saleGetProduct.data)
            
          })
        } catch (e) {
          return callback({ status: 0, message: "HTTPERROR" })
        }
      })
      .catch(error => {
        return callback({ status: 0, message: error })
      })
  }

  static postProduct(data,callback) {
    console.log('postProduct params...',data)
    const query = {
      mutation: gql`
        mutation {
          salePostProduct(
            product_id: "${data._id?data._id:''}", ,
            product_identifier: "${data.product_identifier}",
            product_type: "${data.product_type}",
            product_image: "${data.product_image}",
            product_width: ${data.product_width},
            product_height: ${data.product_height},
            product_length: ${data.product_length},
            product_weight: ${data.product_weight},
            product_weight_unit: "${data.product_weight}",
            product_options:${data.product_options},
            categories:${data.categories},
            product_dismension_unit: "${data.product_dismension_unit}",
            product_brand_id: "${data.product_brand_id}",
            product_approved:  ${data.product_approved?"true":"false"},
            product_added_by_admin_id:  ${data.product_added_by_admin_id?"true":"false"},
            product_description: "${data.product_description}",
            lang_data:${data.lang_data},
          ) {
            status
            message
            success
            data
          }
        }    
      `
    }
    GRAPHQL.doMutate(query)
      .then(response => {
        try {
          return callback({
            status: 1,
            data: JSON.parse(response.data.salePostProduct.data)
          })
        } catch (e) {
          return callback({ status: 0, message: e.Message })
        }
      })
      .catch(error => {
        return callback({ status: 0, message: error })
      })
  }


  static deleteProduct(product_id,callback) {
    const query = {
      mutation: gql`
          mutation{
            saleDeleteProduct(
              product_id:"${product_id}"
            ) {
              status
              message
              success
              data
            }
          }
        `
    }

    GRAPHQL.doMutate(query)
      .then(response => {
        try {
          return callback({
            status: 1,
            data: JSON.parse(response.data.saleDeleteProduct.data)
          })
        } catch (e) {
          return callback({ status: 0, message: e.Message })
        }
      })
      .catch(error => {
        return callback({ status: 0, message: error })
      })
  }

 
}
