import React, { Component, Fragment } from "react";
import IntlMessages from "../../../helpers/IntlMessages";
import { Colxx, Separator } from "../../../components/common/CustomBootstrap";
import Breadcrumb from "../../../containers/navs/Breadcrumb";
import UserService from '../../../service/graphql/User'
import 'react-tabs/style/react-tabs.css';
import {
  Row,
  Card,
  CardBody,
  Input,
  Button,
  Label,
  CustomInput,
  Form
} from "reactstrap";
import "react-tagsinput/react-tagsinput.css";
import "react-datepicker/dist/react-datepicker.css";
import "rc-switch/assets/index.css";
import "rc-slider/assets/index.css";
import "react-rater/lib/react-rater.css";
import AsyncSelect from 'react-select/async'
import CustomSelectInput from "../../../components/common/CustomSelectInput";
import moment from "moment";
import Select from "react-select";
import ProductService from '../../../service/graphql/Product'
import ProductOptionValueService from '../../../service/graphql/ProductOptionValue'
import SellerProductService from '../../../service/graphql/SellerProduct'
import BrandService from '../../../service/graphql/Brand'
import { NotificationManager } from "../../../components/common/react-notifications";
import {plainListToGql,gqlToLanguageOptions,getDefaultLanguageOptions} from '../../../libs/converter'
import LanguageSelector from '../../../components/LanguageSelector'
import Config from '../../../service/api/config'
export default class BlankPage extends Component {
  constructor(props) {
    super(props);

    this.state = {
      selectedOption: "",
      selectedOptionLabelOver: "",
      selectedOptionLabelTop: "",
      startDate: null,
      startDateLabelOver: null,
      startDateLabelTop: null,
      startDateTime: null,
      startDateRange: null,
      endDateRange: null,
      embeddedDate: moment(),
      tags: [],
      tagsLabelOver: [],
      tagsLabelTop: [],
      selectedProductId:null,
      operatorOptions:[],
      selectedItem:{
        product_type:"Physical"
      },
      langs:[],
      langOptions:[],
      selectedLangIndex:0,
      editState:false,
      userOptions:[],
      productOptions:[],
      optionOptions:[],
      optionId:0,
      selectedOptionItem:{},
      productoptionvalues:[],
      selectedOptionValueItem:{},
      isSubmitting:false
    };
  }
  componentDidMount(){
    this.setState({editState:false})
    this.getUsers()
    this.getProducts()
    this.setState({selectedProductId:this.props.match.params.id})
    if(this.props.match.params.id &&  this.props.match.params.id!=="0"){
      SellerProductService.getProduct(this.props.match.params.id,(res)=>{
          this.setState({selectedItem:res.data})
          setImmediate(()=>{
            let {selectedItem} = this.state
            selectedItem.seller_product_option_value_id = res.data?.optionvalue?.optionvalue_id
            this.setState({selectedItem})
          })
          let langOptionsFromProducts = gqlToLanguageOptions(res.data.langs,"seller_product_identifier_label")
          if(langOptionsFromProducts.length===0){
            getDefaultLanguageOptions((options)=>{
              this.setState({langOptions:options})
          })
          }
          this.setState({langOptions:langOptionsFromProducts})
          ProductService.getProduct(res.data.product_id,(product_response)=>{
            let tempOptions = []
            if(product_response.data&&product_response.data.options){
              product_response.data.options.forEach(element => {
                let temp = {
                  value:element.option_id,
                  label:element.option_identifier,
                  key:element.option_id
                }
                tempOptions.push(temp)
            });
          }
          this.setState({optionOptions:tempOptions})
          if(res.data.optionvalue){
            this.getInitialOption(res.data.optionvalue)
          }
        })
      })
    }
    else{
      this.setState({selectedItem:{}})
      getDefaultLanguageOptions((options)=>{
          this.setState({langOptions:options})
      })
    }    
  }
  handleChangeLabelOver = selectedOptionLabelOver => {
    this.setState({ selectedOptionLabelOver });
  };

  getInitialOption(optionvalue){
     const {selectedOptionValueItem,optionOptions}= this.state
     selectedOptionValueItem.value = optionvalue.optionvalue_id
     selectedOptionValueItem.key = optionvalue.optionvalue_id
     selectedOptionValueItem.label = optionvalue.optionvalue_identifier
     this.setState({selectedOptionValueItem})
     setTimeout(()=>{
      let found = optionOptions.find((option)=>{
        return(
          option.value === optionvalue.option_id
        )
      })
      this.setState({selectedOptionItem:found})
     },1000)
     
  }
  onChangeProperty(value,type){
    const {selectedItem} = this.state
    if(type==="seller_product_image"){
      this.setState({editState:true})
      let reader = new FileReader();
      reader.readAsDataURL(value);
      reader.onloadend = () => {
        selectedItem[type] = reader.result
        this.setState({selectedItem})          
      };
    } else if(type==='product_id'){
        selectedItem[type] = value
        this.setState({selectedItem})
        this.getPropductOptions(value)
    }
    else{
      selectedItem[type] = value
      this.setState({selectedItem})
    }
  }
  getUsers = ()=>{
    UserService.getUsers({page:1, pageSize: 100},response=>{
      console.log('users...',response)
        if(response.status === 1){
          const users = response.data.results?response.data.results:[]
          const tempOptions = []
          users.forEach(element => {
              const obj = {
                label:element.full_name,
                value:element._id,
                id:element._id,
              }
              tempOptions.push(obj)
          });
          this.setState({userOptions:tempOptions})
        }
    })
  }
  getProducts = ()=>{
    ProductService.getProducts({page:1, pageSize: 1000}, response=>{
      if(response.status === 1){
        const products = response.data.results
        const tempOptions = []
        products.forEach(element => {
            const obj = {
              label:element.product_title,
              value:element._id,
              id:element._id,
            }
            tempOptions.push(obj)
        });
        this.setState({productOptions:tempOptions})
      }
    })
  }
  
  onChangeName(value){
    const {selectedLangIndex} = this.state
    const selectedLangOption = this.state.langOptions[selectedLangIndex];
    selectedLangOption.title = value
    this.setState({langOptions:[...this.state.langOptions]})
  }
  addNewInstance(){
    const {selectedItem,editState} = this.state
    if((selectedItem.seller_product_identifier === undefined || selectedItem.seller_product_identifier.length === 0) ||
        (selectedItem.product_id === undefined || selectedItem.product_id.length === 0)
    ){
      NotificationManager.error(
        "Error message",
        "Please confirm valid data!",
        3000,
        () => {
          // alert("callback");
        },
        null,
        "error"
      );
      return;
    }
    
    let templangData = plainListToGql(this.state.langOptions)
    this.state.selectedItem.lang_data = templangData
    
    if(!editState){
      selectedItem.seller_product_image = ""
    }

    SellerProductService.postProduct(this.state.selectedItem,(error,res)=>{
      this.props.history.push("/app/sellerproducts/list");
      })
    }
    updateCheckBox(field){
      let {selectedItem} = this.state
      selectedItem[field] = !selectedItem[field]
      this.setState({selectedItem})
   }

   getSelectedOption(value,type){
    let options = []
    if(type==="user"){
        options = this.state.userOptions
    }
    else if(type==="product"){
      options = this.state.productOptions
    }else{

    }
    const found = options.find((item)=>{
      return(
        item.value === value
      )
    })
    return found;
  }
  
  loadProductOptions=(inputValue, callback)=>{
    ProductService.getProducts({page:1, pageSize: 1000}, response=>{
      if(response.status === 1){
          const users = response.data.results
          const tempOptions = []
          users.forEach(element => {
              const obj = {
                label:element.product_title,
                value:element._id,
                id:element._id,
              }
              tempOptions.push(obj)
          });
          this.setState({productOptions:tempOptions})
          setTimeout(() => {
            callback(this.filter(inputValue));
          }, 1000);          
        }
      })
    };
    filter = (inputValue: string) => {
      const {productOptions}= this.state
      return productOptions.filter((i) =>
        i.label.toLowerCase().includes(inputValue.toLowerCase())
      );
    };
    loadUserOptions=(inputValue, callback)=>{
      UserService.getUsers({page:1, pageSize: 1000}, response=>{
        if(response.status === 1){
          const users = response.data.results?response.data.results:[]
          const tempOptions = []
          users.forEach(element => {
              const obj = {
                label:element.full_name,
                value:element._id,
                id:element._id,
              }
              tempOptions.push(obj)
          });
          this.setState({userOptions:tempOptions})
          setTimeout(() => {
            callback(this.filterUsers(inputValue));
          }, 1000);          
        }
      })
    };
    filterUsers = (inputValue: string) => {
      const {userOptions}= this.state
      return userOptions.filter((i) =>
        i.label.toLowerCase().includes(inputValue.toLowerCase())
      );
    };
    handleOption(e){
      this.setState({selectedOptionItem:e})
      ProductOptionValueService.getProductOptionValues(e.value, response=>{
        console.log('productoptionvalues...',response.data)
        if(response.status === 1){
          // this.setState({
          //   productoptionvalues:response.data.results
          // })
          let tempList = []
          response.data.results.forEach(element => {
              let temp = {
                key:element.optionvalue_id,
                value:element.optionvalue_id,
                label:element.optionvalue_identifier
              }
              tempList.push(temp)
          });
          this.setState({productoptionvalues:tempList})
        }
    })
    } 
    handleOptionValueChange(e){
      this.setState({isSubmitting:false})
      this.setState({selectedOptionValueItem:e})
      const {selectedItem}= this.state
      selectedItem.seller_product_option_value_id = e.value
    }
    handleSubmit = (e)=>{
      this.setState({isSubmitting:true})
      e.stopPropagation();
      e.preventDefault();
      const {selectedOptionValueItem,selectedOptionItem,selectedItem} = this.state
      if(!selectedOptionValueItem || !selectedOptionItem || !selectedItem.seller_user_id || !selectedItem.product_id  ){
        return
      }
      this.addNewInstance()
    }
    componentWillReceiveProps(nextProps){
      //  console.log('nextProps...' ,nextProps.selectedItem)
      //  let  productid =  nextProps.selectedItem.product_id
      //  if(productid){
      //   ProductService.getProduct(productid,(product_response)=>{
      //     let tempOptions = []
      //     product_response.data.options.forEach(element => {
      //         let temp = {
      //           value:element.option_id,
      //           label:element.option_identifier,
      //           key:element.option_id
      //         }
      //         tempOptions.push(temp)
      //     });
      //     this.setState({optionOptions:tempOptions})
      //   })
      //  }
    }
    getPropductOptions(id){
      ProductService.getProduct(id,(product_response)=>{
        let tempOptions = []
        if(product_response.data&&product_response.data.options){

          product_response.data.options.forEach(element => {
              let temp = {
                value:element.option_id,
                label:element.option_identifier,
                key:element.option_id
              }
              tempOptions.push(temp)
          });
        }
        this.setState({optionOptions:tempOptions})
      })
    }
    render() {
      const {selectedProductId,selectedItem,editState,isSubmitting} = this.state
        return (
            <Fragment>
              <Row>
                <Colxx xxs="12">
                  <Breadcrumb heading={selectedProductId==='0'?"New Seller Product":"Seller Product Detail"} match={this.props.match} />
                  <Separator className="mb-5" />
                </Colxx>
              </Row>
              <Form onSubmit={this.handleSubmit.bind(this)}>
              <Row className="mb-4">
                <Colxx xxs="12" md="12">
                  <Card>
                    <CardBody className="d-flex">
                      <div className="flex-1">
                        <Label className="mt-4">
                            User
                        </Label>
                        <AsyncSelect
                            required
                            components={{ Input: CustomSelectInput }}
                            className="react-select"
                            name="form-field-name"
                            loadOptions={this.loadUserOptions}
                            defaultOptions
                            value={this.getSelectedOption(selectedItem.seller_user_id,'user')}
                            onChange={(e)=>{this.onChangeProperty(e.value,'seller_user_id')}}
                            onInputChange={this.handleInputChange}
                          />
                        {
                          isSubmitting && !selectedItem.seller_user_id  &&
                          <div className="invalid-feedback d-block">
                                {"User required!"}
                          </div>
                        }
                        <Label className="mt-4">
                            Product
                        </Label>
                        <AsyncSelect
                            required
                            components={{ Input: CustomSelectInput }}
                            className="react-select"
                            name="form-field-name"
                            loadOptions={this.loadProductOptions}
                            defaultOptions
                            value={this.getSelectedOption(selectedItem.product_id,'product')}
                            onChange={(e)=>{this.onChangeProperty(e.value,'product_id')}}
                            onInputChange={this.handleInputChange}
                        />
                        {
                          isSubmitting && !selectedItem.product_id  &&
                          <div className="invalid-feedback d-block">
                                {"Product  required!"}
                          </div>
                        }
                        <Label className="mt-4">
                            Product Option
                        </Label>
                        <Select
                          required
                          components={{ Input: CustomSelectInput }}
                          className="react-select"
                          classNamePrefix="react-select"
                          name="form-field-name"
                          value={this.state.selectedOptionItem}
                          onChange={(e)=>{this.handleOption(e)}}
                          options={this.state.optionOptions}
                        />
                        {
                          isSubmitting && !this.state.selectedOptionItem  &&
                          <div className="invalid-feedback d-block">
                                {"Product Option required!"}
                          </div>
                        }
                        <Label className="mt-4">
                            Option Value
                        </Label>
                        <Select
                          required
                          components={{ Input: CustomSelectInput }}
                          className="react-select"
                          classNamePrefix="react-select"
                          name="form-field-name"
                          value={this.state.selectedOptionValueItem}
                          onChange={(e)=>{this.handleOptionValueChange(e)}}
                          options={this.state.productoptionvalues}
                        />
                        {
                          isSubmitting && !this.state.selectedOptionValueItem &&
                          <div className="invalid-feedback d-block">
                                {"Option value is required!"}
                          </div>
                        }

                        <Label  className="mt-4">
                            Identifier
                        </Label>
                        <Input  required onChange={(e)=>{this.onChangeProperty(e.target.value,'seller_product_identifier')}} value={selectedItem.seller_product_identifier}/>
                        <Label className="mt-4">
                            Description
                        </Label>
                        <Input  required onChange={(e)=>{this.onChangeProperty(e.target.value,'seller_product_description')}} value={selectedItem.seller_product_description}/>
                        <Label className="mt-4">
                            Stock
                        </Label>
                        <Input required  type="number" onChange={(e)=>{this.onChangeProperty(e.target.value,'seller_product_in_stock_count')}} value={selectedItem.seller_product_in_stock_count}/>
                        <Label className="mt-4">
                            Price
                        </Label>
                        <Input required   type="number"  onChange={(e)=>{this.onChangeProperty(e.target.value,'price')}} value={selectedItem.price}/>
                        <div className="d-flex">
                          <Button color="primary" className="mt-4 mr-4" type="submit">
                            <IntlMessages id="forms.submit" />
                          </Button>
                          <Button color="primary" className="mt-4" onClick={()=>{
                              this.props.history.push("/app/sellerproducts/list");
                          }}>
                             Close
                          </Button>
                        </div>
                      </div>
                      <div  className="flex-1">
                        {/* {this.renderLanguageSetting()} */}
                        <Label className="mt-4">
                            Image
                        </Label>
                        <CustomInput
                          type="file"
                          id="exampleCustomFileBrowser1"
                          name="customFile"
                          // onChange={this.setImage}
                          onChange={(e)=>{this.onChangeProperty(e.target.files[0],'seller_product_image')}}
                        />
                        {
                          (selectedItem.seller_product_id && selectedItem.seller_product_image && !editState) &&
                          <img className="product-image" src={Config.BACKEND_API_URL+ selectedItem.seller_product_image}/>
                        }
                        {
                          editState && <img className="product-image" src={selectedItem.seller_product_image}/>
                        }
                        {/* {
                          !selectedItem._id && selectedItem.product_image && 
                          // eslint-disable-next-line jsx-a11y/alt-text
                          <img className="product-image" src={selectedItem.product_image}/>
                        } */}
                        <LanguageSelector 
                          langOptions={this.state.langOptions} 
                          onChangeName={this.onChangeName.bind(this)}
                          selectedLangIndex = {this.state.selectedLangIndex}
                          onChangeTab = {(index)=>{this.setState({selectedLangIndex:index})}}
                        />
                      </div>
                  </CardBody>
                </Card>
              </Colxx>
            </Row>
            </Form>
          </Fragment>
        )
    }
}
