import gql from 'graphql-tag';
import GRAPHQL from './index'
import moment from 'moment'

export default class {

  static getBrands(params, callback) {
    const query = {
      query: gql`
        {
          saleGetBrands(page: ${params.page}, pageSize:${params.pageSize}, lang_code:"${params.langCode}") {
            status
            message
            success
            data
          }
        }
      `,
    };
    GRAPHQL.doQuery(query)
    .then(response => {
      try {
        return callback({
          status: 1,
          data: JSON.parse(response.data.saleGetBrands.data)
        })
      } catch (e) {
        return callback({ status: 0, message: "HTTPERROR" })
      }
    })
    .catch(error => {
      return callback({ status: 0, message: error })
    })
  }

  static getBrandsForSelector(callback) {
    const query = {
      query: gql`
        {
          saleGetBrands {
            status
            message
            success
            data
          }
        }
      `,
    };
    GRAPHQL.doQuery(query)
    .then(response => {
      try {
        return callback({
          status: 1,
          data: JSON.parse(response.data.saleGetBrands.data)
        })
      } catch (e) {
        return callback({ status: 0, message: "HTTPERROR" })
      }
    })
    .catch(error => {
      return callback({ status: 0, message: error })
    })
  }

  
  static postBrand(data,callback) {
    console.log('postBrand...req',data)
    const query = {
      mutation: gql`
          mutation{
            salePostBrand(
              brand_id: "${data.brandId}",
              brand_identifier: "${data.brandIdentifier}",
              brand_seller_id: "${data.brandSellerId}",
              brand_featured: ${data.brandFeatured},
              brand_active: ${data.brandActive},                        
              lang_data:${data.langData}
            ) {
              status
              message
              success
              data
            }
          }
        `
    }
    GRAPHQL.doMutate(query)
      .then(response => {
        console.log("response....",response)
        try {
          return callback({
            status: 1,
            data: JSON.parse(response.data.salePostBrand.data)
          })
        } catch (e) {
          console.log("e on mutate...",e)
          return callback({ status: 0, message: e.Message })
        }
      })
      .catch(error => {
        return callback({ status: 0, message: error })
      })
  }


  static deleteBrand(id,callback) {
    const query = {
      mutation: gql`
          mutation{
            saleDeleteBrand(
              brand_id:"${id}"
            ) {
              status
              message
              success
              data
            }
          }
        `
    }

    GRAPHQL.doMutate(query)
      .then(response => {
        console.log("response....",response)
        try {
          return callback({
            status: 1,
            data: JSON.parse(response.data.saleDeleteBrand.data)
          })
        } catch (e) {
          console.log("e on mutate...",e)
          return callback({ status: 0, message: e.Message })
        }
      })
      .catch(error => {
        return callback({ status: 0, message: error })
      })
  }

 
}
